.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  text-align: center;
}

.App-intro {
  font-size: medium;
  padding-left: 25%;
  padding-right: 25%;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.center {
  display: inline-block;
  margin-left: 5%;
  margin-right: 5%;
}

.lineChart {
  margin: auto;
  -ms-flex-direction: horizontal;
      flex-direction: horizontal;
}

.legend {
  margin-top: -60%;
  margin-left: 80%;
}
.Bubblechart {
  margin: auto;
}

.Linechart {
  display: -ms-flexbox;
  display: flex;
}

.Sankey {
  font-color: 'red';
}

.customHint {
  background: black;
  color: white;
  font-size: 10px;
  padding: 10px;
  border-radius: 3px;
  width: 110px;
}

.rv-hint--orientation-topleft {
  -webkit-transform: translate(-7px,-7px);
      -ms-transform: translate(-7px,-7px);
          transform: translate(-7px,-7px);
}


.rv-hint--orientation-topright {
  -webkit-transform: translate(7px,-7px);
      -ms-transform: translate(7px,-7px);
          transform: translate(7px,-7px);
}


.rv-hint--orientation-bottomleft {
  -webkit-transform: translate(-7px,7px);
      -ms-transform: translate(-7px,7px);
          transform: translate(-7px,7px);
}

.rv-hint--orientation-bottomright {
  -webkit-transform: translate(7px,7px);
      -ms-transform: translate(7px,7px);
          transform: translate(7px,7px);
}



